<template>
  <div class="share-topic-detail full-screen">
    <div class="page-panel">
      <pem-scroll-view ref="scroll"
        :pullup=true
        @pullUpLoad="loadMore"
        class="scroll-view"
        :probeType="3"
        :listenScroll="true">
        <div class="container"  v-show="!isInit">
          <div class="header-open" @click="goDownload">
            <img class="logo" src="http://m.pemarket.com.cn/static/logo.png" alt="">
            <h4 class="title">项目工场</h4>
            <p class="desc">找项目、找投资就到项目工场</p>
            <div class="btn">立即下载</div>
          </div>
          <div class="topic-msgs">
            <img class="logo" :src="topicObj.logo" alt="" @error="defaultLogo($event, 'topiclogo')">
            <img class="follow-icon" src="./guanzhu@2x.png" alt="">
            <h4 class="title">{{ topicObj.name }}</h4>
            <div class="counts">
              <span>动态：{{ topicObj.dtcount }}</span>
              <span class="reads">阅读：{{ topicObj.viewcount }}</span>
            </div>
          </div>
          <div class="dynamic-list">
            <div class="dynamic-item dynamic-msgs" v-for="(msgsObj, index) in dynamicArr" :key="index">
              <div class="part1">
                <img class="avatar" :src="msgsObj.logo" alt=""  @error="defaultLogo">
                <h3 class="name">{{ msgsObj.name }}</h3>
                <p class="time">{{ msgsObj.createtime }}</p>
              </div>
              <div class="desc">{{ msgsObj.content }}</div>
              <!-- 动态图片 -->
              <div class="imgs-wrapper" v-if="msgsObj.imgsArr && msgsObj.imgsArr.length > 0">
                <img class="imgs-item" v-for="(item, index) in msgsObj.imgsArr" :key="index" :src="item" alt="">
              </div>
              <!-- 文字投票 -->
              <ul class="vote-txt-ul" v-if="msgsObj.voteTxtArr && msgsObj.voteTxtArr.length > 0">
                <li class="vote-txt-li" v-for="(item, index) in msgsObj.voteTxtArr" :key="index">
                  <span class="label">{{ item.label }}、</span>{{ item.name }}
                </li>
              </ul>
              <!-- 图片投票 -->
              <ul class="vote-img-ul" v-if="msgsObj.voteImgArr && msgsObj.voteImgArr.length > 0">
                <li class="vote-img-li" v-for="(item, index) in msgsObj.voteImgArr" :key="index" :class="{ 'vote-img-li-more': msgsObj.voteImgArr.length > 3 }">
                  <img class="vote-img-option" :src="item.img" alt="">
                  <div class="vote-img-txt clearfix">{{ item.label }}</div>
                </li>
              </ul>
              <div class="vote-count" v-if="msgsObj.voteCount">{{ msgsObj.voteCount }}人已投票</div>
              <ul class="topic-list" v-if="msgsObj.topicArr && msgsObj.topicArr.length > 0">
                <li class="topic-item" v-for="(item, index) in msgsObj.topicArr" :key="index">{{ item }}</li>
              </ul>
              <ul class="count-list">
                <li class="count-item" @click="judgeGuide"><span class="count-like">{{ msgsObj.likecount }}</span></li>
                <li class="count-item" @click="judgeGuide"><span class="count-reply">{{ msgsObj.commentcount }}</span></li>
                <li class="count-item" @click="judgeGuide"><span class="count-share">{{ msgsObj.sharecount }}</span></li>
              </ul>
            </div>
          </div>
        </div>
      </pem-scroll-view>
      <div class="guide-open-browser" v-if="showGuideMask" @click="closeMask">
        <div class="guide-img"></div>
      </div>
      <div class="footer-open" @click="judgeGuide">打开APP</div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
import PemScrollView from 'components/scroll-view/scroll-view'
import { MiniSiteApi, ImgPath } from 'api'
import { setTitle, device } from 'common/js/util'
import tempUserLogo from './default-avatar.png'
import tempTopicLogo from './moren@2x.png'

export default {
  data () {
    return {
      showGuideMask: false,
      id: '',
      isInit: true,
      imgUrl: ImgPath,
      topicObj: {
        logo: tempTopicLogo,
        name: '',
        dtcount: null,
        viewcount: null
      },
      dynamicArr: []
    }
  },
  components: {
    PemScrollView
  },
  created () {
    this.wxShare({})
    this._initData()
  },
  methods: {
    _initData () {
      if (!device.isWeixin() && device.isAndroid()) {
        window.location.href = `pemarket://www.pemarket.com.cn/attr?type=9&id=${this.$route.query.id}`
      }
      this.id = this.$route.query.id
      this._getTopicDetail({id: this.$route.query.id})
      this._getTopicList({topic_id: this.$route.query.id, currentPage: 1, pageSize: 10})
    },
    goDownload () {
      if (device.isPC()) {
        if (device.isIOS()) {
          window.location.href = 'https://itunes.apple.com/cn/app/id1057083949?mt=8'
        } else {
          window.location.href = 'http://a.app.qq.com/o/simple.jsp?pkgname=com.pemv2'
        }
        return false
      }
      this.$router.push('/download')
    },
    judgeGuide () {
      if (device.isWeixin()) {
        if (device.isIOS()) {
          window.location.href = `https://go.pemarket.com.cn/download-guide?type=9&id=${this.$route.query.id}`
        } else {
          this.showGuideMask = true
        }
      } else {
        this.$router.push('/download')
      }
    },
    closeMask () {
      this.showGuideMask = false
    },
    loadMore () {
      console.log('加载更多')
    },
    defaultLogo (e, type) {
      if (type === 'topiclogo') {
        e.target.src = tempTopicLogo
        return
      }
      e.target.src = tempUserLogo
    },
    _getTopicDetail (options) {
      MiniSiteApi.getTopicDetail(options).then(res => {
        this.isInit = false
        if (+res.resultcode === 1) {
          if (res.logo) {
            this.topicObj.logo = this.imgUrl + res.logo
          }
          this.topicObj.name = res.name || ''
          this.topicObj.dtcount = res.dtcount || '0'
          this.topicObj.viewcount = res.viewcount || '0'
          if (res.name) {
            setTitle(res.name)
          }
          this.wxShare({
            title: res.share_title || null,
            content: res.share_desc || null,
            imgUrl: res.share_icon ? (this.imgUrl + res.share_icon) : null
          })
        }
      }).catch((error) => {
        console.error(error)
      })
    },
    _getTopicList (options) {
      MiniSiteApi.getTopicList(options).then(res => {
        if (+res.resultcode === 1) {
          this.dynamicArr = (res.resultList || []).map(item => {
            let tempItem = {}
            if (item.logo) {
              tempItem.logo = this.imgUrl + item.logo
            } else {
              tempItem.logo = tempUserLogo
            }
            tempItem.name = item.name || ''
            tempItem.createtime = item.createtime || ''
            tempItem.content = item.content || ''
            tempItem.likecount = item.likecount || '0'
            tempItem.commentcount = item.commentcount || '0'
            tempItem.sharecount = item.sharecount || '0'
            if (item.image && item.image.length > 0) {
              tempItem.imgsArr = item.image.map(item2 => {
                item2 = this.imgUrl + item2
                return item2
              })
            }
            if (item.topicList && item.topicList.length > 0) {
              tempItem.topicArr = item.topicList.map(item2 => {
                return (item2.name || '')
              })
            }
            if (item.vote && item.vote.type) {
              let tempObj = item.vote
              tempItem.voteCount = tempObj.vote_count
              if (tempObj.type === 'txt') {
                tempItem.voteTxtArr = tempObj.option || []
              } else {
                tempItem.voteImgArr = (tempObj.option || []).map(item2 => {
                  item2.img = this.imgUrl + item2.img
                  return item2
                })
              }
            }
            return tempItem
          })
        }
      }).catch((error) => {
        console.error(error)
      })
    }
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  @import "~common/stylus/mixin"
  .share-topic-detail
    min-width: 300px
  .page-panel
    top: 0
  .guide-open-browser
    position: fixed
    left: 0
    top: 0
    right: 0
    bottom: 0
    z-index: 1000
    opacity: 0.7
    background: #000000
    .guide-img
      width: 5.12rem
      height: 2.54rem
      margin: 0.48rem auto 0
      bg-image('./bg_guide')
      background-size: 100% 100%
      background-repeat: no-repeat
  .header-open
    position: relative
    height: 60px
    border-bottom: 0.5px solid #f3f3f3
    background: #ffffff
    .logo
      position: absolute 
      left: 23px
      top: 10px
      height: 40px
      width: 40px
      border-radius: 4px;
    .title
      position: absolute 
      left: 71px
      top: 8px
      font-family: PingFangSC-Medium
      font-size: 16px
      font-weight: bold
      color: #41403E
      line-height: 22px
    .desc
      position: absolute 
      left: 71px
      bottom: 10px
      font-family: PingFangSC-Regular
      font-size: 13px
      color: #8B8B8B
      line-height: 18px
    .btn
      position: absolute
      top: 16px
      right: 11px
      width: 78px
      height: 30px
      line-height: 30px
      background: #FFC131
      border-radius: 2px
      font-family: PingFangSC-Regular
      font-size: 15px
      color: #41403E
      letter-spacing: -0.3px
      text-align: center
  .footer-open
    position: fixed
    left: 50%
    bottom: 25px
    margin-left: -79px;
    width: 158px
    padding: 11px 0
    font-family: PingFangSC-Regular;
    font-size: 16px;
    color: #333333;
    line-height: 22px
    text-align: center
    background: #FFC131;
    box-shadow: 0 2px 4px 0 rgba(0,0,0,0.13);
    border-radius: 22px;
  .topic-msgs
    position: relative
    height: 113px
    bg-image('bg')
    background-size: 100% 100%
    .logo
      position: absolute
      left: 20px
      top: 28px
      width: 58px
      height: 58px
      border-radius: 8px;
    .follow-icon
      position: absolute
      right: 20px
      top: 30px
      width: 55px
      height: 22px
      border-radius: 4px
    .title
      position: absolute
      left: 98px
      right: 80px
      top: 28px
      font-family: PingFangSC-Medium;
      font-size: 16px;
      font-weight: bold
      color: #FFFFFF;
      line-height: 22px
    .counts
      position: absolute
      left: 98px
      right: 15px
      bottom: 27px
      font-size: 12px;
      color: #FFFFFF;
      line-height: 17px
      .reads
        margin-left: 20px
  .container
    max-width: 750px;
    margin: 0px auto;
    padding-bottom: 80px
    .dynamic-list
      padding: 10px 10px 25px
    .dynamic-msgs
      padding-bottom: 20px
      border-bottom: 1px solid #ECECEC
      &:last-child
        border: none
      .part1
        position: relative
        height: 71px
        .avatar
          position: absolute
          left: 0
          top: 15px
          width: 44px
          height: 44px
          border-radius: 100%
        .name
          position: absolute
          left: 52px
          top: 16px
          font-family: PingFangSC-Medium;
          font-size: 15px;
          font-weight: bold
          color: #333333;
          line-height: 21px
        .time
          position: absolute
          left: 52px
          bottom: 13px
          font-family: PingFangSC-Regular;
          font-size: 12px;
          color: #A7A7A7;
          line-height: 17px
      .desc
        font-family: PingFangSC-Regular;
        font-size: 14px;
        color: #333333;
        line-height: 22px;
      .imgs-wrapper
        font-size: 0
        .imgs-item
          width: 2rem
          height: 2rem
          margin: 15px 0.3rem 0 0
          &:nth-child(3n + 0)
            margin-right: 0
      .vote-txt-ul
        padding-top: 4px
        .vote-txt-li
          position: relative
          margin-top: 12px
          padding: 9px 52px 9px 38px
          border: 1px solid #EEEEEE;
          border-radius: 4px
          font-size: 14px;
          color: #333333;
          line-height: 20px
          .label
            position: absolute
            left: 14px
            top: 9px
          .rate
            position: absolute
            right: 15px
            top: 9px
            font-size: 12px;
            color: #999999;
            line-height: normal
      .vote-img-ul
        font-size: 0
        .vote-img-li
          position: relative
          display: inline-block
          margin: 15px 0.3rem 0 0
          width: 2rem
          padding-top: 2rem
          &.vote-img-li-more
            width: 2.5rem
            padding-top: 2.5rem
            .vote-img-option
              width: 2.5rem
              height: 2.5rem
          .vote-img-option
            position: absolute
            top: 0
            left: 0
            width: 2rem
            height: 2rem
            border-radius: 2px;
          .vote-img-txt
            margin-top: 8px
            padding: 0 5px 0 9px
            border: 0.5px solid #EEEEEE;
            border-radius: 2px;
            height: 24px
            line-height: 24px
            font-size: 15px;
            color: #333333
            .rate
              font-size: 11px;
              color: #999999;
      .vote-count
        font-size: 11px;
        color: #999999;
        line-height: 16px
        margin-top: 8px
      .topic-list
        font-size: 0
        padding: 20px 0 0
        .topic-item
          display: inline-block
          height: 21px
          line-height: 21px
          padding: 0 6px 0 21px
          margin: 0 12px 8px 0
          font-size: 12px;
          color: #333333;
          background: #F2F3F2;
          border-radius: 10px;
          bg-image('dynamic-huati')
          background-size: 14px 14px
          background-repeat: no-repeat
          background-position: 5px center
          &:last-child
            margin-right: 0
      .count-list
        overflow: hidden
        text-align: center
        margin-top: 20px;
        .count-item
          width: 33.33%
          float: left
          font-size: 12px;
          color: #666666;
          .count-like, .count-reply, .count-share
            display: inline-block
            padding-left: 21px
            line-height: 17px
            background-size: 14px 14px
            background-repeat: no-repeat
            background-position: left center
          .count-like
            bg-image('dynamic-zan')
          .count-reply
            bg-image('dynamic-reply')
          .count-share
            bg-image('dynamic-share')
</style>



